$sucess-color: #30724b;
$secondary-color: #e4e4e4;
$white-color: #ffffff;
$warning-color: #fbe34c;
$black-color: #000000;
$main-gradient: linear-gradient(to left, #da4453, #800080);
$card-hover-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
  rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
$card-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
a.navbar-brand {
  white-space: normal;
  text-align: center;
  word-break: break-all;
}

html {
  font-size: 14px;
}

@media (min-width: 768px) {
  html {
    font-size: 16px;
  }
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  text-align: center;
}

.flex-item-left {
  flex: 45%;
}

.flex-item-right {
  flex: 45%;
}

@media (min-width: 1001px) {
  .presentation-card {
    width: 60vw;
  }
}

@media (max-width: 1000px) {
  .education-card,
  .certification-card,
  .presentation-card,
  .experience-card {
    width: 80vw;
  }
}

@media (max-width: 768px) {
  .education-card,
  .certification-card,
  .presentation-card,
  .experience-card {
    width: 90vw;
  }
  .flex-item-right,
  .flex-item-left {
    flex: 100%;
    text-align: center;
  }
  .navbar-nav {
    margin-left: 0px;
  }

  .options {
    display: flex;
    flex-direction: column-reverse;
  }
  .options > * {
    margin-top: 3px;
  }
  .under-cons-picture {
    width: 70vw;
  }
  .picture-404 {
    width: 35vw;
  }

  .presentation-card {
    z-index: 2;
    height: 150px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    border-radius: 7px;
    box-shadow: $card-shadow;
    color: $black-color;
    padding: 5px;
    margin-top: 0px;
    border-top: 2px solid $white-color;
    backdrop-filter: blur(10px);
  }
  .presentation-picture {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: relative;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    top: 25px;
    border: 2px solid $white-color;
    z-index: 3;
    transition: 0.5s;
  }
}

.box-shadow {
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);
}

.logo {
  height: 50px;
  width: 100px;
}
.img-logo {
  width: 50px;
  height: 50px;
}

.navbar-nav {
  margin-left: 50px;
}

.d-sm-inline-flex {
  z-index: 100;
}

.divider {
  padding-right: 10px;
  border-left: 2px solid $secondary-color;
}
.divider-right {
  border-right: 2px solid $secondary-color;
}

.modal-divider {
  border-bottom: 2px solid $white-color;
}

.menu {
  color: $main-gradient;
}
.menu-selected {
  border-bottom: 5px solid;
  border-image: $main-gradient 1;
}
.menu > h1 {
  font-size: 50px;
  border-bottom: 5px solid;
}

.user {
  color: #505050;
}
.form-control {
  background-color: #eaeaea;
}

.btn-success {
  background-color: $sucess-color;
}

.modal-backdrop.show {
  background: blur(10px);
}

.triangle-bottom-left {
  display: inline-block;
  width: 0;
  height: 0;
  position: fixed;
  bottom: 0%;
  left: 0%;
  border-style: solid;
  border-width: 400px 0 0 700px;
  border-color: transparent transparent transparent $sucess-color;
  z-index: -1;
}

.modal-background {
  background-color: $sucess-color;
}
.modal-background > * {
  color: $white-color;
}
.white-content {
  color: $white-color;
}
.btn-outline-success {
  border-color: $white-color;
}
.btn {
  height: 35px;
  align-items: center;
  justify-content: center;
}
.btn-outline-primary {
  background-color: $white-color;
  border-color: $sucess-color;
  color: $sucess-color;
  display: flex;
}
.btn-outline-primary:hover {
  background-color: $sucess-color;
  border-color: inherit;
  color: $white-color;
}

.btn-outline-primary:active:focus {
  box-shadow: #9dccb6 0px 0px 0px 3px;
}
.btn-outline-primary:focus {
  box-shadow: none;
}

.btn-outline-warning {
  background-color: warning-color;
  color: $sucess-color;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-outline-warning:hover {
  background-color: #f5ec5a;
  border-color: warning-color;
  color: inherit;
}

.btn-outline-warning:active:focus {
  box-shadow: #f5ec5a 0px 0px 0px 3px;
}
.btn-outline-warning:focus {
  box-shadow: none;
}

.form-control:focus:focus {
  box-shadow: none;
  border-color: #eaeaea;
}
.header-table {
  background-color: #eaeaea;
}

.header-cv {
  color: $white-color;
  height: 110px;
  border-radius: 0 0 100% 100%;
  background: $main-gradient;
}

.header-cv > * {
  text-align: center;
}

.header-cv > h1 {
  visibility: hidden;
}

.education-card {
  position: relative;
  height: 120px;
  margin-bottom: 5px;
  border-radius: 7px;
  box-shadow: $card-shadow;
  padding: 5px;
  margin-left: auto;
  margin-right: auto;
  transition: 0.1s;
}
.education-card:hover,
.education-card:active,
.education-card:focus,
.certification-card:hover,
.certification-card:active,
.certification-card:focus {
  box-shadow: $card-hover-shadow;
}

.certification-card {
  position: relative;
  height: max-content;
  border-radius: 7px;
  box-shadow: $card-shadow;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
}

.certification-card > * {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 575px) {
.presentation-card {
  z-index: 2;
  height: 150px;
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 7px;
  box-shadow: $card-shadow;
  color: $black-color;
  padding: 5px;
  margin-top: 0px;
  border-top: 2px solid $white-color;
  backdrop-filter: blur(10px);
}

.presentation-picture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 75px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid $white-color;
  z-index: 3;
  transition: 0.5s;
}
}

.presentation-card > h4 {
  padding-top: 30px;
}

.presentation-picture:hover {
  transform: scale(1.3);
}

.skill {
  display: inline-block;
  color: $white-color;
  border-radius: 50rem;
  margin-right: 5px;
  margin-bottom: 5px;
}
.skill-language {
  background-color: #81017f;
}
.skill-framework {
  background-color: #9c1572;
}
.skill-db {
  background-color: #c3335d;
}
.skill-tools {
  background-color: #d94353;
}
.skill > span {
  padding: 0.35em 0.65em;
  line-height: 2;
  text-align: center;
}

.options {
  position: fixed;
  right: 5px;
  bottom: 15px;
  color: $white-color;
  z-index: 4;
}

.options > button,
a > button {
  margin-right: 5px;
  border-radius: 7px;
  color: #08acb4;
  background-color: #252933;
  border: none;
  width: 50px;
  height: 50px;
  transition: 0.2s;
}
.options :hover {
  color: #ffffff;
}
.options :active {
  font-size: 20px;
}

.footer {
  background-color: $black-color;
  display: flex;
  justify-content: center;
  color: $white-color;
  border-top: 5px solid #800080;
}

.footer > * {
  margin: 10px;
}
.social-footer {
  cursor: pointer;
  font-size: 1.5em;
  color: $white-color;
  transition: 0.4s;
}

.social-footer:hover {
  color: #800080;
}

/* Experiences components */

.experience-card {
  height: fit-content;
  margin-bottom: 10px;
  border-radius: 7px;
  padding: 5px;
  margin-left: auto;
  margin-right: auto;
  transition: 0.1s;
  position: relative;
  box-shadow: $card-shadow;
}
.experience-card:hover,
.experience-card:active,
.experience-card:focus {
  box-shadow: $card-hover-shadow;
}

.experience-header {
  position: absolute;
  width: inherit;
  height: 50px;
  left: -20px;
  top: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.experience-picture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
  box-shadow: $card-shadow;
}

.experience-description {
  margin-top: 60px;
  text-align: justify;
  padding: 5px;
  white-space: pre-line;
}

.experience-details {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.bi-stack,
.bi-person-workspace,
.bi-geo-alt-fill,
.bi-calendar-range-fill,
.bi-mortarboard-fill {
  margin-right: 5px;
}

.certification-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skeleton-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.certification-picture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid $white-color;
  box-shadow: $card-shadow;
}

.hidden {
  display: none;
}

.center-div {
  justify-content: center;
}

img{
  image-rendering: -webkit-optimize-contrast;
}
